@import '~src/css/quasar.variables.scss', 'quasar/src/css/variables.sass';
















































































$theBorder: solid rgba(255, 255, 255, 0.1) 1px;
.splitSession {
  border-top: $theBorder;
  border-bottom: $theBorder;
}

.verticalSplit {
  border-left: $theBorder;
  border-right: $theBorder;
}

.leftSplit {
  border-left: $theBorder;
}

